<template>
<el-card>
    <div slot="header">
        <span>GMT日志</span>
    </div>
    <!-- 搜索 -->

    <div>
        <el-form :model="objSearch" label-width="100px">
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="操作员">
                        <el-input v-model="objSearch.adminName" placeholder="请输入操作员"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="服务器">
                        <el-input v-model="objSearch.serverId" placeholder="请输入服务器"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="操作指令">
                        <el-select v-model="objSearch.method" placeholder="请选择操作指令">
                            <el-option v-for="(ele,idx) in aMethods" :label="ele.name" :value="ele.method" :key="idx"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="handleSearch">搜索</el-button>
                    <el-button type="primary" @click="handleReset">重置</el-button>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-form-item label="开始">
                        <el-date-picker v-model="objSearch.start" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择开始时间"></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="结束">
                        <el-date-picker v-model="objSearch.end" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择结束时间"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
    <!-- 表格 -->
    <div>
        <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%">
            <el-table-column prop="id" label="ID" min-width="100"></el-table-column>
            <el-table-column prop="time" label="时间" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="serverIds" label="服务器id" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="ip" label="IP" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="method" label="操作指令" min-width="200" show-overflow-tooltip></el-table-column>
            <el-table-column prop="content" label="内容" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="adminName" label="操作员" min-width="100" show-overflow-tooltip></el-table-column>
            <!-- <el-table-column prop="useNum" label="时间" min-width="100" show-overflow-tooltip></el-table-column>
        <el-table-column prop="noUseNum" label="未使用" min-width="100" show-overflow-tooltip>
          <template slot-scope="scope">
            <span>{{scope.row.num-scope.row.useNum}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" min-width="100">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="handleClickUpdate(scope.row)"
              type="text"
              size="small"
            >修改</el-button>
          </template>
        </el-table-column> -->
        </el-table>
        <!-- 分页 -->
        <el-pagination class="pagination" background layout="prev, pager, next" @current-change="handlePageChange" :current-page.sync="objPager.currentPage" :page-size.sync="objPager.pageSize" :total="objPager.total"></el-pagination>
    </div>
</el-card>
</template>

<script>
import axios from "axios";
export default {
    name: "order",
    components: {},
    props: {},
    data() {
        return {
            aMethods: [],
            objSearch: {},
            aTableData: [],
            // 节点分页数据
            objPager: {
                total: 0,
                currentPage: 1,
                pageSize: 10
            }
        };
    },
    watch: {},
    methods: {
        // 重置
        handleReset() {
            this.objSearch = {};
        },
        //   搜索
        handleSearch() {
            this.fetchList();
        },

        // 分页
        handlePageChange(e) {
            this.objPager.currentPage = e;
            this.fetchList();
        },
        handleClickUpdate() {
        },
        // 列表
        fetchList() {
            let params = JSON.parse(JSON.stringify(this.objSearch));
            params = {
                ...params,
                page: this.objPager.currentPage,
                pageSize: this.objPager.pageSize
            };
            this.$fetch.post(API.gmt.gm_log, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aTableData = res.data.inData;
                    this.objPager.total = res.data.total;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        // 指令字典
        fetchMethod() {
            this.$fetch.post(API.gmt.method_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aMethods = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchMethod();
    }
};
</script>

<style lang="stylus" scoped>
.pagination {
    text-align: right;
    margin-top: 15px;
}
</style>
